import React, { useState } from "react";
import { Button } from "@mui/material";

import EnterprisePopUp from "../../components/EnterprisePopUp";

const CustomPlan = ({
  title,
  subtitle,
  imgSrc,
  imgAlt,
  features,
  isMobile,
}) => {
  const [showAll, setShowAll] = useState(false),
    [enterpriseModal, setEnterpriseModal] = useState(false),
    openEnterpriseModal = () => setEnterpriseModal(true),
    closeEnterpriseModal = (event, reason) => {
      setEnterpriseModal(false);
    };

  return (
    <div className={`plan-box custom-plan`}>
      <h4 className="title">{title}</h4>
      <h5 className="subtitle">{subtitle}</h5>
      <div className="icon">
        <img src={imgSrc} alt={imgAlt} />
      </div>
      <div className="special">
        <Button variant="outlined" type="button" onClick={openEnterpriseModal}>
          {"Talk to us"}
        </Button>
      </div>
      <div className="ruler">
        <hr />
      </div>
      <ul
        className={`features${
          isMobile ? (showAll ? " show-all" : " show-less") : ""
        }`}
      >
        {features.map((feature, index) => {
          return (
            <li key={index} className={feature.includes("*") ? "star" : ""}>
              {feature.includes("*") ? feature.slice(1) : feature}
            </li>
          );
        })}
      </ul>
      {isMobile && (
        <div className={`expand${showAll ? " up" : " down"}`}>
          <button type="button" onClick={() => setShowAll(!showAll)}>
            {showAll ? "SHOW LESS FEATURES" : "SEE ALL FEATURES"}
          </button>
        </div>
      )}
      <EnterprisePopUp open={enterpriseModal} onClose={closeEnterpriseModal} />
    </div>
  );
};

export default CustomPlan;
