import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  popup: {
    "& .MuiDialog-paper": {
      backgroundColor: "#FFFFFF",
      border: "1px solid rgba(0, 0, 0, 0.1)",
      boxShadow: "0px 5px 40px rgba(18, 176, 238, 0.1)",
      borderRadius: 0,

      "& .MuiDialogTitle-root": {
        color: "#24272C",
        padding: "20px 40px",
        fontWeight: 900,
        fontSize: "20px",
        lineHeight: "24px",
      },

      "& .MuiDialogContent-root": {
        color: "#24272C",
        padding: "0 40px",
        maxHeight: "55vh",

        [`${theme.breakpoints.down("md")} and (orientation: landscape)`]: {
          maxHeight: "33vh",
        },

        "& h4": {
          fontWeight: 900,
          fontSize: "16px",
          lineHeight: "20px",
        },

        "& p": {
          fontWeight: 400,
          fontSize: "10px",
          lineHeight: "14px",
        },

        "& blockquote": {
          padding: "10px 10px 20px 20px",

          "& h5": {
            fontWeight: 500,
            fontSize: "16px",
            lineHeight: "24px",
          },

          "& a": {
            color: "#12B0EE",
            fontWeight: 500,
            fontSize: "12px",
            lineHeight: "24px",
            textDecoration: "none",
          },
        },
      },

      "& .MuiDialogActions-root": {
        justifyContent: "center",
        padding: "20px 40px",

        "& .MuiButton-containedPrimary": {
          alignItems: "center",
          backgroundColor: "#12B0EE",
          boxShadow: "none",
          borderRadius: "8px",
          display: "flex",
          fontSize: "14px",
          fontWeight: 700,
          height: "48px",
          justifyContent: "center",
          lineHeight: "14px",
          padding: 0,
          textTransform: "capitalize",
          width: "180px",
        },
      },
    },
  },
  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {},
  [theme.breakpoints.up("md")]: {},
  [theme.breakpoints.up("lg")]: {},
  [theme.breakpoints.up("xl")]: {},
}));

export default useStyles;
