import React, { useState } from "react";
import SecureImg from "../assets/secure.svg";
import BasicImg from "../assets/basic.svg";
import SmartImg from "../assets/smart.svg";
import ProfessionalImg from "../assets/professional.svg";
import EnterpriseImg from "../assets/enterprise.svg";

import Plan from "./plan";
import CustomPlan from "./custom-plan";

const Chatbot = ({ className, isMobile, plans }) => {
  const [check, setCheck] = useState(false),
    customPlan = {
      title: "Enterprise",
      subtitle: "FORTUNE 500",
      imgSrc: EnterpriseImg,
      imgAlt: "Enterprise",
      features: [
        "*Extreme Customization",
        "*Priority support",
        "*Fixed priced contract with annual billing.",
        "Advanced chatbot builder",
        "Unlimited Contacts",
        "Broadcast & Templates",
        "Unlimited Agents",
        "Customized Analytics",
        "Enterprise grade data security",
        "APIs & Webhooks",
        "Free Incoming Messages",
        "Unlimited conversations per month",
        "Includes 15 messages / conversation",
        "20p/msg delivery fee apart from official WhatsApp template broadcast messages.",
        "Email / WhatsApp support",
      ],
    };

  return (
    <section className={className}>
      <div className="content">
        <div className="plans-header">
          <span className="badge">{"ENGAGE"}</span>
          <h2 className="title">{"WhatsApp ChatBot"}</h2>

          <div className="plan-changer">
            <span className={!check ? "label active" : "label"}>MONTHLY</span>
            <div className="switch">
              <input
                type="checkbox"
                className="control"
                id="change-plan"
                defaultChecked={check}
              />
              <label
                aria-label="Change Plan"
                className="view"
                htmlFor="change-plan"
                onClick={() => setCheck(!check)}
              />
            </div>
            <span className={check ? "label active" : "label"}>ANNUAL</span>
          </div>

          {false && (
            <p className="statement">
              Save <em>upto 20%</em> on annual plans!
            </p>
          )}
          <br />
          <br />
          <div className="offer-block">
            <div className="icon">
              <img src={SecureImg} alt="secure" />
            </div>
            <div className="info">
              <h5>MONEY BACK GUARANTEE</h5>
              <p>
                Your first 200 conversations are on us. If you don’t like us,
                you get <strong>Full Refund.</strong>
              </p>
            </div>
          </div>
        </div>
        <div className="plans-lists">
          {plans.map((plan, index) => {
            if (!check && plan.plan_type === "Monthly") {
              return (
                <Plan
                  key={index}
                  currentPlan={plan.planId}
                  isMobile={isMobile}
                  {...plan}
                />
              );
            }
            if (check && plan.plan_type === "Annual") {
              return (
                <Plan
                  key={index}
                  currentPlan={plan.planId}
                  isMobile={isMobile}
                  {...plan}
                />
              );
            }
          })}

          <CustomPlan isMobile={isMobile} {...customPlan} />
        </div>
      </div>
      <div className="additional-cost">
        <div className="header">
          <h6>Additional Costs</h6>
        </div>
        <ul className="list">
          <li className="item">
            <div className="info">
              <strong>{"Broadcast Messages (Template Messages)"}</strong>
              <p>
                {
                  "WhatsApp charges a per message fee on business initiated template messages, the price varies on usage and country."
                }
              </p>
            </div>
            <a href="/">{"view pricing"}</a>
          </li>
          <li className="item">
            <div className="info">
              <strong>{"Green Tick Approval"}</strong>
              <p>
                {
                  "We will attempt to get a green tick approval for your business, you would be allotted  verification if your business meets guidelines set by whatsapp."
                }
              </p>
            </div>
            <a href="/">{"$50"}</a>
          </li>
        </ul>
      </div>
    </section>
  );
};

export default Chatbot;
